import { Component, OnInit } from '@angular/core';
import { LiffService } from '../services/liff.service';
import { GattData } from '../data/gatt.data';
import { BufferData } from '../data/buffer.data';

declare var liff: any;
const SERVICE_UUID = GattData.USER_SERVICE_UUID;
const URINE_CHARACTERISTIC_UUID = GattData.URINE_JUDGMENT_DATA_UUID;

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit {
  countSensor: number;

  constructor(
    private liffService: LiffService,
  ) {
    this.countSensor = null;
  }

  ngOnInit() {
    this.liffService.initLiff()
    .then(() => {
      this.initLineLiff();
    })
    .catch((err) => {
      alert('ngOnInit Error' + JSON.stringify(err));
    });
  }

  initLineLiff() {
    this.liffService.initLineLiff()
    .then(() => {
      this.liffCheckAvailabilityAndDo(() => this.liffRequestDevice());
    })
    .catch((error) => {
      alert('initLineLiff Error' + JSON.stringify(error));
    });
  }

  liffCheckAvailabilityAndDo(callbackIfAvailable) {
    this.liffService.getAvailability().then((isAvailable) => {
      if (isAvailable) {
        callbackIfAvailable();
      } else {
        setTimeout(() => this.liffCheckAvailabilityAndDo(callbackIfAvailable), 1000);
      }
    })
    .catch((error) => alert('liffCheckAvailabilityAndDo' + JSON.stringify(error)));
  }

  liffRequestDevice() {
    this.liffService.requestDevice().then((device) => {
      this.liffConnectToDevice(device);
    })
    .catch((error) => {
      alert('liffRequestDevice ERROR' + JSON.stringify(error));
    });
  }

  liffConnectToDevice(device) {
    device.gatt.connect().then(async () => {
      const service = await device.gatt.getPrimaryService(SERVICE_UUID);
      const characteristic = await service.getCharacteristic(URINE_CHARACTERISTIC_UUID);
      await characteristic.addEventListener('characteristicvaluechanged', (e) => {
        this.jugmentCountSensor(e);
      });
      await characteristic.startNotifications();
    }).catch((connectRrror) => alert('Connect Error: ' + JSON.stringify(connectRrror)));
    const disconnectCallback = () => {
      device.removeEventListener('gattserverdisconnected', disconnectCallback);
      this.initLineLiff();
    };
  }

  jugmentCountSensor(e) {
    if (BufferData.count0.value.indexOf(e.target.value.getInt8()) > -1) {
      if (this.countSensor !== BufferData.count0.countSensor) {
       this.countSensor = BufferData.count0.countSensor;
      }
      alert(BufferData.count0.message);
    } else if (BufferData.count1.value.indexOf(e.target.value.getInt8()) > -1) {
      if (this.countSensor !== BufferData.count1.countSensor) {
        this.countSensor = BufferData.count1.countSensor;
       }
      alert(BufferData.count1.message);
    } else if (BufferData.count2.value.indexOf(e.target.value.getInt8()) > -1) {
      if (this.countSensor !== BufferData.count2.countSensor) {
        this.countSensor = BufferData.count2.countSensor;
       }
      alert(BufferData.count2.message);
    } else if (BufferData.count3.value.indexOf(e.target.value.getInt8()) > -1) {
      if (this.countSensor !== BufferData.count3.countSensor) {
        this.countSensor = BufferData.count3.countSensor;
      }
      alert(BufferData.count3.message);
    } else if (BufferData.count4.value.indexOf(e.target.value.getInt8()) > -1) {
      if (this.countSensor !== BufferData.count4.countSensor) {
        this.countSensor = BufferData.count4.countSensor;
      }
      alert(BufferData.count4.message);
    }
  }
}
