import { Injectable } from '@angular/core';
import { GattData } from '../data/gatt.data';
import { LiffData } from '../data/liff.data';

const SERVICE_UUID = GattData.USER_SERVICE_UUID;
const URINE_CHARACTERISTIC_UUID = GattData.URINE_JUDGMENT_DATA_UUID;
const PSDI_SERVICE_UUID = GattData.PSDI_SERVICE_UUID;
const PSDI_CHARACTERISTIC_UUID = GattData.PSDI_CHARACTERISTIC_UUID;
const LIFF_ID = LiffData.LIFF_ID;

declare var liff: any;

@Injectable({
  providedIn: 'root'
})
export class LiffService {

  constructor() { }

  initLiff(): Promise<any> {
    return liff.init({ liffId: LIFF_ID });
  }
  initLineLiff(): Promise<any> {
    return liff.initPlugins(['bluetooth']);
  }
  getAvailability(): Promise<any> {
    return liff.bluetooth.getAvailability();
  }
  requestDevice(): Promise<any> {
    return liff.bluetooth.requestDevice();
  }

}
